:root {
  --pc: #011735;
  --sc: #F5CB9B;
  --body-color: #000;
  --gray-bg: #E5E7E9;
  --footer-bg: #272727;
  --white: #FFFFFF;
  --tf: transparent;
  --p-color: #999999;
  --h1toh6: #4E4E4D;
  --line-light: #D9D9D9;
  --btn-hover: red;
}

:root {
  --ani-2: all 0.2s 0s ease;
  --ani-3: all 0.3s 0s ease;
  --ani-4: all 0.4s 0s ease;
  --ani-5: all 0.5s 0s ease;
  --op-thr: 0.3;
  --op-n: 0.9;
  --op-e: 0.8;
  --op-f: 0.5;
  --font-t: 100;
  --font-el: 200;
  --font-l: 300;
  --font-r: 400;
  --font-m: 500;
  --font-sb: 600;
  --font-b: 700;
  --font-eb: 800;
  --font-black: 900;
  --font-eblack: 950;
}

body,
html {
  font-family: "Alegreya";
  line-height: 1.5;
  font-size: 24px;
  color: var(--body-color);
}

* {
  font-family: "Alegreya";
}

.h1,
h1,
.h2,
h2,
.h3,
h3,
.h4,
h4,
.h5,
h5,
.h6,
h6 {
  color: var(--h1toh6);
  line-height: 1.2;
}

.h1,
h1 {
  font-weight: var(--font-sb);
  font-size: 2.2083333333rem;
}

.h2,
h2 {
  font-weight: var(--font-sb);
  font-size: 1.5rem;
}

.h3,
h3 {
  font-weight: var(--font-sb);
  font-size: 1.25rem;
}

.h4,
h4 {
  font-weight: var(--font-sb);
  font-size: 1rem;
}

.h5,
h5 {
  font-weight: var(--font-m);
  font-size: 0.875rem;
}

.h6,
h6 {
  font-weight: var(--font-b);
  font-size: 0.6666666667rem;
}

p {
  color: var(--p-color);
  font-weight: var(--font-r);
  line-height: 1.4;
}

a {
  color: var(--pc);
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

svg {
  width: 100%;
  height: auto;
}

.btn {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

@media (max-width: 1920px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl,
.container-xxl {
    max-width: 69.75rem;
  }
}
.btn-primary.pc, .btn-primary.sticky_white, .btn-primary.white, .btn-primary.white-line {
  background-color: var(--pc);
  background: var(--pc);
  border: unset;
  padding: 0.625rem 2.0833333333rem;
  border-radius: 8.3333333333rem;
  font-size: 1rem;
  font-weight: var(--font-r);
  color: #fff;
  line-height: 1.5;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0 0.3333333333rem;
  grid-gap: 0 0.3333333333rem;
  transition: var(--ani-t);
}
.btn-primary.pc img, .btn-primary.sticky_white img, .btn-primary.white img, .btn-primary.white-line img {
  flex: 0 0 1.6366666667rem;
  width: 1.6366666667rem;
}
.btn-primary.white-line {
  background-color: var(--tf);
  border: 1px solid var(--white);
  color: var(--white);
}
.btn-primary.white {
  background-color: var(--white);
  color: var(--pc);
}
.btn-primary.sticky_white {
  background-color: var(--white);
  border-radius: unset;
  color: var(--pc);
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 9999;
}

.kira_form .form-control {
  background-color: var(--tf);
  padding: 0.4166666667rem 0;
  border-radius: unset;
  border: unset;
  border-bottom: 1px solid rgba(255, 255, 255, 0.431372549);
  box-shadow: none;
  outline: none;
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: #ffffff;
}
.kira_form .form-control.placeholder {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: rgba(255, 255, 255, 0.431372549);
}
.kira_form .form-control:-moz-placeholder {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: rgba(255, 255, 255, 0.431372549);
}
.kira_form .form-control::-moz-placeholder {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: rgba(255, 255, 255, 0.431372549);
}
.kira_form .form-control:-ms-input-placeholder {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: rgba(255, 255, 255, 0.431372549);
}
.kira_form .form-control::-webkit-input-placeholder {
  font-size: 0.75rem;
  font-weight: var(--font-r);
  color: rgba(255, 255, 255, 0.431372549);
}
.kira_form select.form-control:focus {
  color: var(--pc);
}
.kira_form .form-group:last-child {
  grid-area: 4/1/5/3;
}
.kira_form .form-group-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0.8333333333rem 1.375rem;
  margin-bottom: 2.2916666667rem;
}

.kira_header {
  text-align: center;
  position: relative;
  z-index: 1;
}
.kira_header_h2 {
  font-family: "ITC Zapf Chancery";
  font-size: 2.1666666667rem;
  font-weight: var(--font-b);
  color: var(--white);
  position: relative;
  display: inline-block;
  margin-bottom: 0.25rem;
}
.kira_header_h2 * {
  font-family: "ITC Zapf Chancery";
}
.kira_header_h2 span {
  color: #F5CB9B;
}
.kira_header_h2_light {
  display: inline-block;
  width: 1.5416666667rem;
  mix-blend-mode: color;
}
.kira_header_h2_light_1 {
  margin-right: -1.6666666667rem;
  position: absolute;
  top: 0;
  left: -0.5833333333rem;
  z-index: 1;
}
.kira_header_h2_light_2 {
  margin-right: -1.6666666667rem;
  position: relative;
  z-index: 1;
}
.kira_header_p {
  font-size: 0.8333333333rem;
  font-weight: var(--font-r);
