  max-width: 50.5rem;
  margin-inline: auto;
}
.kira_header_img {
  width: 22.2916666667rem;
  margin-inline: auto;
}

.lt-zoom-in {
  border-radius: 0.25rem;
  overflow: hidden;
  transition: all 0.5s ease;
  position: relative;
}
.lt-zoom-in::before {
  content: "";
  position: absolute;
  width: 0%;
  display: block;
  background-color: var(--sc);
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  transition: all 1s ease;
}
.lt-zoom-in:hover::before {
  transition: all 1s ease;
  left: 100%;
  width: 100%;
}
.lt-zoom-in:hover img {
  transition: all 0.5s ease;
  transform: scale(1.2);
}
.lt-zoom-in img {
  transition: all 0.5s ease;
}

.kira_slider_arrow {
  width: 2.9166666667rem;
  height: 2.9166666667rem;
  border-radius: 100%;
  background-color: var(--white);
  padding: 0.625rem;
  display: flex;
  z-index: 2;
  position: absolute;
  top: 50%;
  transform: translate(50%, -50%);
}
.kira_slider_arrow_prev {
  left: 0%;
  -webkit-transform: scaleX(-1) translate(50%, -50%);
  transform: scaleX(-1) translate(50%, -50%);
}
.kira_slider_arrow_next {
  right: 0%;
}
.main_section {
